.whole-division{
    height: 80px;
}

.header-div{
    background-color: rgb(88, 88, 88);
    height: 100%;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.dropdown-div{
    align-items: center;
    margin-left: 2.75%;
    display: flex;
    width: 9%;
}

.leftdropdown{
    cursor: pointer;
    transform: scale(1.75);
    margin-left: 20px;
}

.header{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}

.rightdropdown{
    cursor: pointer;
    transform: rotate(180deg);
    transform: scale(1.75);
    margin-left: 20px;
}

.logo{
    height: 95%;
    width: 8%;
}

.slider{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 25vh;
    width: 15%;
    margin: 10px;
    margin-left: 3.5%;
    background-color: rgb(12, 239, 247);
}

.Testimonials{
    display: flex;
    justify-content: center;
    color: black;
}

.dropdown-last{
    margin-left: 72.75%;
}

.right{
    right: 0;
    margin-right: 3.5%;
}

.Testimonials-link{
    text-decoration: none;
    color: black;
}